@media (max-width: 767px) {
  .btn-svg svg,
.btn-svg-faq svg {
    width: 1.875rem;
    height: 1.875rem;
  }
}
.btn-svg svg path,
.btn-svg-faq svg path {
  stroke: #102654;
}
.btn-svg svg rect,
.btn-svg-faq svg rect {
  stroke: #102654;
}
@media (hover: hover) {
  .btn-svg:hover:not(.focus-visible) circle,
.btn-svg-faq:hover:not(.focus-visible) circle {
    stroke: #102654;
  }
}
.btn-svg:focus circle,
.btn-svg-faq:focus circle {
  stroke: #102654;
}
.btn-svg:focus:not(.focus-visible) circle,
.btn-svg-faq:focus:not(.focus-visible) circle {
  stroke: #102654;
}
.btn-svg:active:not(.focus-visible) svg,
.btn-svg-faq:active:not(.focus-visible) svg {
  fill: #102654;
}
.btn-svg:active:not(.focus-visible) circle,
.btn-svg-faq:active:not(.focus-visible) circle {
  stroke: #102654;
}
.btn-svg:active:not(.focus-visible) path,
.btn-svg-faq:active:not(.focus-visible) path {
  stroke: white;
}
.btn-svg:disabled,
.btn-svg-faq:disabled {
  cursor: not-allowed;
  pointer-events: none;
}
.btn-svg:disabled path,
.btn-svg-faq:disabled path {
  opacity: 0.3;
}

.btn-svg-faq:hover svg {
  fill: #102654;
}
.btn-svg-faq:hover path {
  stroke: #ffffff;
}

